
Template.body.addContent((function() {
  var view = this;
  return "";
}));
Meteor.startup(Template.body.renderToDocument);

Template.__checkName("ApplicationLayout");
Template["ApplicationLayout"] = new Template("Template.ApplicationLayout", (function() {
  var view = this;
  return [ Blaze._TemplateWith(function() {
    return "header";
  }, function() {
    return Spacebars.include(view.lookupTemplate("yield"));
  }), "\n	\n	", HTML.DIV({
    "class": "container"
  }, "\n	", Blaze._TemplateWith(function() {
    return "main";
  }, function() {
    return Spacebars.include(view.lookupTemplate("yield"));
  }), "\n	") ];
}));

Template.__checkName("navbar");
Template["navbar"] = new Template("Template.navbar", (function() {
  var view = this;
  return HTML.NAV({
    "class": "navbar navbar-default"
  }, "\n  ", HTML.DIV({
    "class": "container-fluid"
  }, "\n    ", HTML.Raw('<div class="navbar-header">\n      <a class="navbar-brand" href="/">\n        Minstant!\n      </a>\n    </div>'), "\n    ", HTML.DIV({
    "class": "nav navbar-nav"
  }, "\n    ", Spacebars.include(view.lookupTemplate("loginButtons")), "\n"), "\n  "), "\n");
}));

Template.__checkName("lobby_page");
Template["lobby_page"] = new Template("Template.lobby_page", (function() {
  var view = this;
  return Spacebars.include(view.lookupTemplate("available_user_list"));
}));

Template.__checkName("available_user_list");
Template["available_user_list"] = new Template("Template.available_user_list", (function() {
  var view = this;
  return [ HTML.Raw("<h2>Choose someone to chat with:</h2>\n	"), HTML.DIV({
    "class": "row"
  }, "\n	", Blaze.Each(function() {
    return Spacebars.call(view.lookup("users"));
  }, function() {
    return [ "\n	", Spacebars.include(view.lookupTemplate("available_user")), "\n	" ];
  }), "\n") ];
}));

Template.__checkName("available_user");
Template["available_user"] = new Template("Template.available_user", (function() {
  var view = this;
  return HTML.DIV({
    "class": "col-md-2 col-sm-2"
  }, "\n		", HTML.DIV({
    "class": "user_avatar"
  }, "\n			", Blaze.If(function() {
    return Spacebars.dataMustache(view.lookup("isMyUser"), view.lookup("_id"));
  }, function() {
    return [ " \n			", HTML.DIV({
      "class": "bg-success"
    }, Blaze.View("lookup:getUsername", function() {
      return Spacebars.mustache(view.lookup("getUsername"), view.lookup("_id"));
    }), " (YOU)\n				", HTML.BR(), "\n				", HTML.IMG({
      src: function() {
        return [ "/", Spacebars.mustache(Spacebars.dot(view.lookup("profile"), "avatar")) ];
      },
      "class": "avatar_img"
    }), "			\n			"), "\n			" ];
  }, function() {
    return [ "\n			", HTML.A({
      href: function() {
        return [ "/chat/", Spacebars.mustache(view.lookup("_id")) ];
      }
    }, "\n				", Blaze.View("lookup:getUsername", function() {
      return Spacebars.mustache(view.lookup("getUsername"), view.lookup("_id"));
    }), "\n				", HTML.BR(), "\n				", HTML.IMG({
      src: function() {
        return [ "/", Spacebars.mustache(Spacebars.dot(view.lookup("profile"), "avatar")) ];
      },
      "class": "avatar_img"
    }), "\n			"), "\n			" ];
  }), "\n		"), "\n	");
}));

Template.__checkName("chat_page");
Template["chat_page"] = new Template("Template.chat_page", (function() {
  var view = this;
  return [ HTML.Raw('<h2>Type in the box below to send a message!</h2>\n    <div class="row">\n        <div class="col-md-12">\n            <div class="well well-lg">\n                <blockquote>\n                    <a href="http://emoji.codes/" target="_blank">Click to see Emoji Cheat Sheet</a>\n                    <footer>Then hover over each emoticon/emoji to get its short name so can type it anywhere in your text message . For exmple: :smile: for smiley face, :angry: for angry face, etc. (notice the ":" before and after the short name). Exmple text message: \'Greetings from outerspace :alien:\'</footer>\n                </blockquote>\n            </div>\n        </div>\n    </div>\n    '), HTML.DIV({
    "class": "row"
  }, "\n        ", HTML.DIV({
    "class": "col-md-12"
  }, "\n            ", HTML.DIV({
    "class": "well well-lg"
  }, "\n                ", Blaze.Each(function() {
    return Spacebars.call(view.lookup("messages"));
  }, function() {
    return [ "\n                ", Spacebars.include(view.lookupTemplate("chat_message")), "\n                " ];
  }), "\n            "), "\n        "), "\n    "), HTML.Raw('\n    <div class="row">\n        <div class="col-md-12">\n            <form class="js-send-chat">\n                <input class="input" type="text" name="chat" placeholder="type a message here...">\n                <button class="btn btn-default">send</button>\n            </form>\n        </div>\n    </div>') ];
}));

Template.__checkName("chat_message");
Template["chat_message"] = new Template("Template.chat_message", (function() {
  var view = this;
  return [ HTML.IMG({
    src: function() {
      return [ "/", Spacebars.mustache(view.lookup("userId2Avatar"), view.lookup("source")) ];
    },
    "class": "avatar_img"
  }), "\n     \n	", Blaze.View("lookup:userId2Name", function() {
    return Spacebars.mustache(view.lookup("userId2Name"), view.lookup("source"));
  }), " said: ", Spacebars.include(view.lookupTemplate("emojione"), function() {
    return [ " ", Blaze.View("lookup:text", function() {
      return Spacebars.mustache(view.lookup("text"));
    }), " " ];
  }), HTML.Raw("\n	<br>") ];
}));
